.project-page
{
    background-size:cover;
    background-repeat:no-repeat;
    padding:0;
    background-color:#eee;
}

.project-page-content main{
    padding-top:0px;
}

.project-page h2{
    margin-top:0px;
    padding-top:20px;
    font-family: 'Abel', sans-serif;
    text-decoration:underline;
    position:relative;
    font-weight:500;
    font-size:2.2em;
    margin-bottom:10px;
}

.project-page h3{
    margin-top:0px;
    padding-top:20px;
    font-family: 'Abel', sans-serif;
    position:relative;
    font-weight:500;
    font-size:1.8em;
}

.project-page h4{
    font-size: 1.3em;
}

