/*=============================================
// Header Classes
//===========================================*/

/*Overall Header Container*/
.project-header
{
    width:100%;
    background-color:#000;
    font-size:0; /*removes our gap*/
    height:70vh;
    position: relative;
}

/*Button to download, read more, etc.*/
.project-header button{
    border: 1px solid white;
    padding: 12px;
    background-color:dodgerblue;
    color:white;
    font-size:0.5em;
    font-weight: bold;
    display:block;
    margin: 0 auto;

    -webkit-transition: all 50ms ease;
    -moz-transition: all 50ms ease;
    -o-transition: all 50ms ease;
    -ms-transition: all 50ms ease;
    transition: all 50ms ease;
}

.project-header button:hover{
    background-color:rgb(86, 166, 247);
}

.project-header a:hover{
    text-decoration:none;
}

.project-header a:active{
    color:inherit;
}

/*Contains button and logo*/
.project-header-wrapper
{
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    top:0;
    left:0;
    justify-content:center;
    z-index:5;
    position: absolute;
    font-size:2rem;
}

/*Logo div within our content box*/
.project-header-content
{
    margin:0 auto;
    text-align:center;
}

/*Styling for our logo*/
.project-header-logo-wrapper{
    width: 100%;
    min-width: 10rem;
    max-width: 27rem;
    margin: 0 auto;
}

/*Picture background within header*/
.project-header picture{
    display:flex;
    height:inherit;
}


/*Default fallback img located in picture tag*/
.project-header img{
    object-fit: cover;
    height: auto;
    width: 100%;
}

/*=============================================
// Page Content Classes
//===========================================*/

/*Wraps all project content to 1200px*/
.project-page-content
{
    max-width:1200px;
    margin:0 auto;
    background-color:rgba(0, 0, 0, 0.4);
}

.project-pad{
    padding-left:20px;
    padding-right:20px;
}

.project-downloads ul, .project-links ul
{
    padding-left:20px;
}

.project-downloads li, .project-links li{
    list-style: none;
}

.project-page-content i{
    margin-right: 0.5em;
}

/*Content overlay that darkens the background image*/
.project-page-wrapper
{
    background-color:rgba(0, 0, 0, 0.6);
}

.trailer-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
}
.trailer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.media-container
{
    display: block;
    min-height: 1px;
    width: 100%;
    /* border: 1px solid rgb(221, 221, 221); */
    overflow: auto;
}

.media-container img {
    filter: brightness(100%);
    -webkit-filter: brightness(100%);
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -o-transition: all 1s ease;
    -ms-transition: all 1s ease;
    transition: all 1s ease;
}

.media-container img:hover {
    filter: brightness(50%);
    -webkit-filter: brightness(50%);
    
}

.project-media
{
    margin-top:20px;
    border-top:1px solid black;
}